'use strict';

const population = document.querySelectorAll('.population');
console.log(population);

const populationNumber = [...population].map(
  item => Number(item.innerText.replace(/[,]/g, ''))
);
console.log(populationNumber);

const totalPopulation = populationNumber.reduce(
  (accumulator, currentValue) =>
    accumulator + currentValue, 0
);
console.log(totalPopulation);

const averagePopulation = totalPopulation / populationNumber.length;
console.log(averagePopulation);

const total = document.querySelector('.total-population');
const average = document.querySelector('.average-population');

total.innerText = totalPopulation.toLocaleString('en-EN');
average.innerText = averagePopulation.toLocaleString('en-EN');
